export default {
  label: [{ required: true, message: '', trigger: 'blur' }],
  model: [
    { required: true, message: '', trigger: 'blur' },
    {
      validator (
        rule: object | object[],
        value: any,
        callback: (e?: any) => void
      ) {
        const reg = /^[a-zA-Z]\w*$/
        if (reg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }
    }
  ],
  width: [
    {
      validator (
        rule: object | object[],
        value: any,
        callback: (e?: any) => void
      ) {
        if (value && typeof value !== 'number') {
          callback(new Error(''))
        }
        if (value) {
          const num = parseInt(value, 10)
          if (num < 100 || num > 400) {
            callback(new Error(' 100px ~ 400px'))
          }
        }
        callback()
      }
    }
  ]
}
